<template>
  <Panel>
    <template #title></template>
    <template #default>
      <el-row :gutter="20" justify="space-between" class="row-mb">
        <el-col :md="12" :offset="0">
          <ItemGroup>
            <el-select
              style="width: 320px"
              v-model="currentRemoteUuid"
              filterable
              placeholder=""
              size="small"
              @change="remoteSelectHandle"
            >
              <el-option
                v-for="item in remoteList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <el-input
              v-model="query.instanceName"
              placeholder=""
              size="small"
              style="width: 160px"
            ></el-input>
            <el-button size="small" @click="refresh">
              <i class="el-icon-refresh"></i> 
            </el-button>
          </ItemGroup>
        </el-col>
        <el-col :md="12" :offset="0">
          <ItemGroup style="text-align: right">
            <el-button size="small" type="success" @click="toNewInstance">
              <i class="el-icon-plus"></i> 
            </el-button>
            <el-button size="small" @click="batOpen">
              <i class="el-icon-video-play"></i> 
            </el-button>
            <el-button size="small" @click="batStop">
              <i class="el-icon-video-pause"></i> 
            </el-button>
            <el-button size="small" @click="batKill">
              <i class="el-icon-video-pause"></i> 
            </el-button>
            <el-button size="small" type="danger" plain @click="batDelete(1)">
              <i class="el-icon-delete"></i> 
            </el-button>
            <el-button size="small" type="danger" @click="batDelete(2)">
              <i class="el-icon-delete"></i> 
            </el-button>
          </ItemGroup>
        </el-col>
      </el-row>

      <div>
        <div class="instance-table-warpper">
          <div>
            <div class="color-red" v-if="!currentRemoteUuid">
              &nbsp;
            </div>
          </div>
          <div>
            <el-pagination
              background
              layout="prev, pager, next"
              :total="maxPage"
              v-model:currentPage="page"
              :page-size="1"
              @current-change="handleCurrentChange"
              small
            ></el-pagination>
          </div>
        </div>

        <!--  -->
        <div v-show="!currentRemoteUuid">
          <div class="notAnyInstanceTip">
            <i class="el-icon-guide" style="font-size: 190px"></i>
            <div class="sub-title">
              <div class="sub-title-title"></div>
              <div class="sub-title-info">
                 localhost
                
              </div>
            </div>
          </div>
        </div>

        <!--  -->
        <div v-show="notAnyInstance && page === 1">
          <div class="notAnyInstanceTip">
            <i class="el-icon-truck" style="font-size: 190px"></i>
            <div class="sub-title">
              <div class="sub-title-title"></div>
              <div class="sub-title-info">
                 Minecraft
                
              </div>
            </div>
          </div>
        </div>

        <el-table
          :data="instances"
          stripe
          style="width: 100%"
          size="mini"
          ref="multipleTable"
          @selection-change="selectionChange"
          v-show="!notAnyInstance && currentRemoteUuid"
        >
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="nickname" label="" min-width="240">
            <template #default="scope">
              <div
                @click="toInstance(scope.row.serviceUuid, scope.row.instanceUuid)"
                class="instanceTitle"
              >
                {{ scope.row.nickname }}
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="currentPlayers" label="" width="240">
            <template #default="scope">
              <div>
                <span v-if="scope.row.info && scope.row.info.currentPlayers >= 0">
                  : {{ scope.row.info.currentPlayers }}/{{ scope.row.info.maxPlayers }}
                </span>
                <span v-if="scope.row.info && scope.row.version">
                  &nbsp;: {{ scope.row.version }}
                </span>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="status" label="" width="120">
            <template #default="scope">
              <div class="color-gray" v-if="scope.row.status == 0">
                <i class="el-icon-video-pause"></i>
                <span> </span>
              </div>
              <div class="color-green" v-else-if="scope.row.status == 3">
                <i class="el-icon-video-play"></i>
                <span> </span>
              </div>
              <span class="color-yellow" v-else-if="scope.row.status == 1"></span>
              <span class="color-yellow" v-else-if="scope.row.status == 2"></span>

              <span class="color-red" v-else-if="scope.row.status == -1"></span>
              <span class="color-red" v-else></span>
            </template>
          </el-table-column>
          <el-table-column prop="type" label="" width="140"></el-table-column>
          <el-table-column label="" style="text-align: center" width="180">
            <template #default="scope">
              <el-button
                size="mini"
                @click="editInstance(scope.row.serviceUuid, scope.row.instanceUuid)"
              >
                
              </el-button>
              <el-button
                size="mini"
                @click="toInstance(scope.row.serviceUuid, scope.row.instanceUuid)"
              >
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </template>
  </Panel>
</template>

<style scoped>
.instanceTitle {
  cursor: pointer;
}

.instanceTitle:hover {
  color: rgb(20, 128, 230);
}
.notAnyInstanceTip {
  text-align: center;
  margin: 100px 0px;
}
</style>

<script>
import { CircleCheckFilled, CircleCloseFilled } from "@element-plus/icons";
import Panel from "../../components/Panel";
// import LineLabel from "../../components/LineLabel";
import { ElMessage } from "element-plus";
import axios from "axios";
import { API_INSTANCE, API_SERVICE_INSTANCES, API_SERVICE_LIST, API_URL } from "../service/common";
import router from "../router";
import { request } from "../service/protocol";
import { typeTextToReadableText } from "../service/instance_tools";
export default {
  // eslint-disable-next-line vue/no-unused-components
  components: { Panel, CircleCheckFilled, CircleCloseFilled },
  data() {
    return {
      remoteList: [],
      currentRemoteUuid: null,
      instances: [],
      multipleSelection: [], // 
      startedInstance: 0,
      loading: true,
      availableService: [], // 
      unavailableService: [],

      notAnyInstance: false, // 

      page: 1,
      maxPage: 1,

      query: {
        instanceName: ""
      }
    };
  },
  async mounted() {
    await this.render();
  },
  beforeUnmount() {},
  methods: {
    // 
    async displayRemoteServiceList() {
      const data = await request({
        method: "GET",
        url: API_SERVICE_LIST
      });
      for (const service of data) {
        const ip = `${service.ip}:${service.port}`;
        const remarks = `${service.remarks}`;
        if (service.available) {
          this.remoteList.push({
            value: service.uuid,
            label: `${ip} ${remarks}`,
            available: true
          });
          this.availableService.push(service);
        } else {
          this.remoteList.push({
            value: service.uuid,
            label: `${ip} ${remarks} ()`,
            available: false
          });
          this.unavailableService.push(service);
        }
      }

      // 
      const lastSelected = localStorage.getItem("pageSelectedRemoteUuid");
      if (lastSelected) {
        this.remoteList.forEach((v) => {
          if (v.value === lastSelected) {
            this.currentRemoteUuid = v.value;
            return this.remoteSelectHandle();
          }
        });
      } else {
        this.remoteList.forEach((v) => {
          if (v.available) {
            // 
            this.currentRemoteUuid = v.value;
            return;
          }
        });
        this.remoteSelectHandle();
      }
    },
    // 
    async remoteSelectHandle() {
      try {
        if (!this.currentRemoteUuid) throw new Error("");
        this.startedInstance = 0;
        this.instances = [];
        this.loading = true;
        const result = await request({
          method: "GET",
          url: API_SERVICE_INSTANCES,
          params: {
            remote_uuid: this.currentRemoteUuid,
            page: this.page,
            page_size: 20,
            instance_name: this.query.instanceName
          }
        });
        // 
        this.page = result.page;
        this.maxPage = result.maxPage;
        const instances = result.data;
        instances.forEach((instance) => {
          const status = instance.status;
          const type = typeTextToReadableText(instance.config.type);
          // 
          if (instance.status != 0) this.startedInstance++;
          // 
          this.instances.push({
            instanceUuid: instance.instanceUuid,
            serviceUuid: this.currentRemoteUuid,
            nickname: instance.config.nickname,
            info: instance.info,
            currentPlayers: instance.info ? instance.info.currentPlayers : "0",
            version: instance.info ? instance.info.version : "",
            type,
            status
          });
        });
        console.log(this.instances);
        this.loading = false;
        // 
        localStorage.setItem("pageSelectedRemoteUuid", this.currentRemoteUuid);

        // 
        if (this.instances.length === 0) {
          this.notAnyInstance = true;
        } else {
          this.notAnyInstance = false;
        }
      } catch (error) {
        this.$notify({
          title: "",
          message: error.toString(),
          type: "error"
        });
      }
    },
    // 
    handleCurrentChange() {
      this.refresh();
    },
    refresh() {
      this.remoteSelectHandle();
    },
    async render() {
      await this.displayRemoteServiceList();
    },
    // 
    selectionChange(v) {
      if (v.length == 0) this.canInterval = true;
      else this.canInterval = false;
      this.multipleSelection = v;
    },
    editInstance(serviceUuid, instanceUuid) {
      console.log(":", serviceUuid, instanceUuid);
      router.push({ path: `/instance_detail/${serviceUuid}/${instanceUuid}/` });
    },
    toNewInstance() {
      if (!this.currentRemoteUuid) {
        return this.$message({ type: "info", message: "" });
      }
      router.push({ path: `/new_instace/${this.currentRemoteUuid}` });
    },
    toInstance(serviceUuid, instanceUuid) {
      console.log(":", serviceUuid, instanceUuid);
      router.push({ path: `/terminal/${serviceUuid}/${instanceUuid}/` });
    },
    // 
    async batDelete(type) {
      if (type === 1) {
        await this.$confirm(
          "",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }
        );
      } else {
        await this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        });
      }

      const uuids = [];
      for (const iterator of this.multipleSelection) {
        uuids.push(iterator.instanceUuid);
      }
      if (uuids.length === 0) {
        return this.$message({ type: "error", message: "" });
      }
      await axios.request({
        method: "DELETE",
        url: API_INSTANCE,
        params: {
          remote_uuid: this.currentRemoteUuid
        },
        data: { uuids, deleteFile: type === 1 ? false : true }
      });
      this.$notify({
        title: "",
        message: ""
      });
    },
    async batKill() {
      if (this.multipleSelection.length == 0)
        return ElMessage.error("");
      await axios.request({
        method: "POST",
        url: `${API_URL}/api/instance/multi_kill/`,
        data: this.multipleSelection
      });
      this.$notify({
        title: "",
        message: ""
      });
    },
    async batOpen() {
      if (this.multipleSelection.length == 0)
        return ElMessage.error("");
      await axios.request({
        method: "POST",
        url: `${API_URL}/api/instance/multi_open/`,
        data: this.multipleSelection
      });
      this.$notify({
        title: "",
        message: ""
      });
    },
    async batStop() {
      if (this.multipleSelection.length == 0)
        return ElMessage.error("");
      await axios.request({
        method: "POST",
        url: `${API_URL}/api/instance/multi_stop/`,
        data: this.multipleSelection
      });
      this.$notify({
        title: "",
        message: ""
      });
    }
  }
};
</script>
