<template>
  <Panel>
    <template #title></template>
    <template #default>
      <div class="flex flex-space-between">
        <ItemGroup>
          <el-button type="success" size="small" @click="openNewServiceDialog">
            
          </el-button>
          <el-button size="small" @click="refresh"></el-button>
        </ItemGroup>
        <ItemGroup>
          <el-button size="small" @click="openPrinciplePanel"></el-button>
        </ItemGroup>
      </div>
    </template>
  </Panel>
  <Panel>
    <template #title></template>
    <template #default>
      <p>
        
        <br />
         localhost
        IPIP
        <br />
        {{ specifiedDaemonVersion }}
      </p>
      <el-table :data="services" style="width: 100%" size="small">
        <el-table-column label="" width="170">
          <template #default="scope">
            <el-input size="small" v-model="scope.row.ip" placeholder=""></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" width="110">
          <template #default="scope">
            <el-input size="small" v-model="scope.row.port" placeholder=""></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" width="210">
          <template #default="scope">
            <span
              >{{ scope.row.remarks }}
              <i style="cursor: pointer" class="el-icon-edit" @click="updateRemarks(scope.row)"></i
            ></span>
            <!-- <el-input size="small" v-model="scope.row.remarks"></el-input> -->
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template #default="scope">
            <div v-if="scope.row.system">{{ scope.row.system.platform }}</div>
          </template>
        </el-table-column>
        <el-table-column label="CPU">
          <template #default="scope">
            <div v-if="scope.row.system">
              {{ Number(scope.row.system.cpuUsage * 100).toFixed(1) }}%
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="160">
          <template #default="scope">
            <div v-if="scope.row.system">{{ scope.row.system.memText }}</div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <div v-if="scope.row.instance">
              {{ scope.row.instance.running }}/{{ scope.row.instance.total }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <span
              class="color-green"
              v-if="scope.row.version && scope.row.version === specifiedDaemonVersion"
            >
              <i class="el-icon-circle-check"></i> {{ scope.row.version }}
            </span>
            <span class="color-red">
              <el-tooltip
                effect="dark"
                v-if="scope.row.version !== specifiedDaemonVersion"
                placement="top"
                content=""
              >
                <span><i class="el-icon-warning-outline"></i> {{ scope.row.version }}</span>
              </el-tooltip>
            </span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <span class="color-green" v-if="scope.row.available">
              <i class="el-icon-circle-check"></i> 
            </span>
            <span class="color-red" v-if="!scope.row.available">
              <el-tooltip effect="dark" content="ip" placement="top">
                <span><i class="el-icon-warning-outline"></i> </span>
              </el-tooltip>
            </span>
          </template>
        </el-table-column>
        <el-table-column label="" style="text-align: center" width="260px">
          <template #default="scope">
            <el-button size="mini" @click="linkService(scope.row, true)">
              {{ scope.row.available ? "" : "" }}
            </el-button>
            <el-button size="mini" @click="updateKey(scope.row, true)"></el-button>
            <el-button size="mini" type="danger" plain @click="deleteService(scope.row.uuid)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </template>
  </Panel>

  <Dialog v-model="isNewService">
    <template #title></template>
    <template #default>
      <div>
        <div class="sub-title"></div>
        <el-input
          v-model="newServiceInfo.remarks"
          placeholder=""
          size="small"
        ></el-input>
        <div class="sub-title row-mt">
          <div class="sub-title-title">IP</div>
          <div class="sub-title-info">
            <b></b> localhost 
          </div>
        </div>
        <el-input
          v-model="newServiceInfo.ip"
          placeholder=" mcsmanager.com43.123.211.12"
          size="small"
        ></el-input>
        <div class="sub-title row-mt"></div>
        <el-input
          v-model="newServiceInfo.port"
          placeholder=" 24444"
          size="small"
        ></el-input>
        <div class="sub-title row-mt">
          <div class="sub-title-title"></div>
          <div class="sub-title-info">
            
            <br />
            <a href="https://docs.mcsmanager.com/" class="color-blue">  </a>
          </div>
        </div>
        <el-input
          v-model="newServiceInfo.apiKey"
          placeholder=" 6ff0fa1ef9a943f3c6f2fe0e4564a2fa383d35c4b78ccb5"
          size="small"
        ></el-input>
        <div class="row-mt">
          <ItemGroup>
            <el-button type="success" size="small" @click="toNewService(false)"></el-button>
            <el-button @click="isNewService = !isNewService" size="small">  </el-button>
          </ItemGroup>
        </div>
      </div>
    </template>
  </Dialog>

  <Dialog v-model="isNewServiceWarning">
    <template #title></template>
    <template #default>
      <div class="sub-title">
        <div class="sub-title-title">
          IP :{{ newServiceInfo.ip }}
        </div>
        <div class="sub-title-info">
          
        </div>
      </div>
      <div class="sub-title">
        <div class="sub-title-title">IP</div>
        <div class="sub-title-info">
          Web <br />
          IP
        </div>
      </div>
      <div class="sub-title"></div>
      <div style="text-align: center">
        <img :src="require('../../assets/connect.png')" alt="" srcset="" style="height: 230px" />
      </div>
      <div class="sub-title row-mt">
        <div class="sub-title-title">IP</div>
        <div class="sub-title-info"></div>
      </div>
      <div class="row-mt">
        <ItemGroup>
          <el-button type="danger" size="small" @click="toNewService(true)"></el-button>
          <el-button @click="isNewServiceWarning = !isNewServiceWarning" size="small"
            ></el-button
          >
        </ItemGroup>
      </div>
    </template>
  </Dialog>

  <Dialog v-model="isOpenPrinciplePanel">
    <template #title></template>
    <template #default>
      <div class="sub-title">
        <div class="sub-title-info">
          Web  
          <br />
          
        </div>
      </div>
      <div class="sub-title"></div>
      <div style="text-align: center">
        <img :src="require('../../assets/principle.png')" alt="" srcset="" style="height: 460px" />
      </div>

      <div class="sub-title">
        <div class="sub-title-info">
          
        </div>
      </div>
      <div class="row-mt">
        <ItemGroup>
          <el-button type="success" size="small" @click="isOpenPrinciplePanel = false">
            
          </el-button>
        </ItemGroup>
      </div>
    </template>
  </Dialog>
</template>

<script>
import Panel from "../../components/Panel";
import Dialog from "../../components/Dialog";
import axios from "axios";
import { API_OVERVIEW, API_SERVICE_CURD, API_SERVICE_URL } from "../service/common";
import { ERROR_TEXT, OK_TEXT } from "../service/text";
import { request } from "../service/protocol";

export default {
  components: { Panel, Dialog },
  data() {
    return {
      newServiceInfo: {
        ip: "",
        port: "",
        apiKey: "",
        remarks: ""
      },
      services: [],
      drawer: false,
      selectRow: null,
      isNewService: false,
      isNewServiceWarning: false,
      isOpenPrinciplePanel: false,

      panelVersion: null,
      specifiedDaemonVersion: null
    };
  },
  methods: {
    // 
    async refresh() {
      await this.render();
      this.$message({ type: "info", message: "", duration: 400 });
    },
    // 
    async render() {
      const result = await request({
        method: "GET",
        url: API_OVERVIEW
      });
      result.remote.forEach((element) => {
        if (element.system) {
          // 
          const free = Number(element.system.freemem / 1024 / 1024 / 1024).toFixed(1);
          const total = Number(element.system.totalmem / 1024 / 1024 / 1024).toFixed(1);
          const used = Number(total - free).toFixed(1);
          element.system.memText = `${used}G/${total}G`;
        }
      });
      this.services = result.remote;
      // 
      this.specifiedDaemonVersion = result.specifiedDaemonVersion;
      this.panelVersion = result.version;
    },
    // 
    async toNewService(enforce = false) {
      const addr = this.newServiceInfo.ip;
      if (
        addr.indexOf("192.") == 0 ||
        addr.indexOf("172.16") == 0 ||
        addr.indexOf("10.") == 0 ||
        addr.indexOf("172.31") == 0
      ) {
        if (!enforce) {
          this.isNewServiceWarning = true;
          return;
        } else {
          this.isNewServiceWarning = false;
        }
      }
      try {
        if (this.newServiceInfo.remarks === "") this.newServiceInfo.remarks = "Remote Host";
        await request({
          method: "POST",
          url: API_SERVICE_CURD,
          data: this.newServiceInfo
        });
        this.isNewService = false;
        this.$message({ type: "success", message: OK_TEXT });
        this.render();
      } catch (err) {
        this.$message({ type: "error", message: ERROR_TEXT });
      }
    },
    // 
    openNewServiceDialog() {
      this.isNewService = true;
    },
    // 
    async linkService(row, isNeedSave) {
      try {
        if (isNeedSave) await this.updateService(row);
        await request({
          url: `${API_SERVICE_URL}/link_remote_service`,
          method: "GET",
          params: { uuid: row.uuid }
        });
        setTimeout(this.refresh, 500);
        this.$message({ type: "success", message: OK_TEXT });
      } catch (error) {
        this.$message({ type: "error", message: error.message });
      }
    },
    // 
    async updateService(row) {
      await axios.put(
        API_SERVICE_CURD,
        { ip: row.ip, port: row.port, remarks: row.remarks || "Remote Host" },
        {
          params: { uuid: row.uuid }
        }
      );
    },
    // 
    async deleteService(uuid) {
      await this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      });
      try {
        axios.delete(API_SERVICE_CURD, { params: { uuid } });
        this.$message({ type: "success", message: OK_TEXT });
        this.render();
      } catch (err) {
        this.$message({ type: "error", message: ERROR_TEXT });
      }
    },
    // 
    async updateRemarks(row) {
      const text = await this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      });
      row.remarks = text.value;
      try {
        await this.updateService(row);
        this.$message({ type: "success", message: "" });
      } catch (error) {
        this.$message({ type: "error", message: error });
      }
    },
    // 
    async updateKey(row) {
      let text = await this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      });
      text = String(text.value);
      text = text.trim();
      try {
        await axios.put(
          API_SERVICE_CURD,
          {
            apiKey: text
          },
          {
            params: { uuid: row.uuid }
          }
        );
        // this.$message({ type: "success", message: "" });
        await this.linkService(row, false);
      } catch (error) {
        this.$message({ type: "error", message: error });
      } finally {
        setTimeout(() => this.refresh(), 1000);
      }
    },
    openPrinciplePanel() {
      this.isOpenPrinciplePanel = true;
    }
  },
  async mounted() {
    await this.render();
  }
};
</script>

<style scoped>
.collapse-item-title-table {
  width: 100%;
}

.service-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
