<template>
  <Panel v-show="step == 1">
    <template #title></template>
    <template #default>
      <div class="flex flex-space-between flex-align-items-center">
        <div>
          <ItemGroup>
            <el-button type="" size="small" @click="back"></el-button>
            <el-button type="" size="small" @click="toProgress"></el-button>
          </ItemGroup>
        </div>
        <span class="color-gray">&nbsp;&nbsp;</span>
      </div>
      <LineOption class="row-mt" :custom="true">
        <template #default>
          <div class="sub-title"></div>
          <div class="sub-title-info">
             Minecraft  Java/Python/.Net 
            
          </div>
          <div class="sub-title row-mt"> Docker</div>
          <div class="sub-title-info">
            Docker
            
            
          </div>
        </template>
      </LineOption>
      <div class="row-mt">
        <el-row :gutter="10">
          <el-col :span="6" :offset="0">
            <SelectBlock style="min-height: 120px" @click="selectType(1)">
              <template #title> OpenJDK 8 </template>
              <template #info
                > Java 8  Java  Minecraft 1.17
                </template
              >
            </SelectBlock>
          </el-col>
          <el-col :span="6" :offset="0">
            <SelectBlock style="min-height: 120px" @click="selectType(2)">
              <template #title> OpenJDK 16 </template>
              <template #info> Java 16  Minecraft 1.17 </template>
            </SelectBlock>
          </el-col>
          <el-col :span="6" :offset="0">
            <SelectBlock style="min-height: 120px" @click="selectType(5)">
              <template #title> OpenJDK 17 </template>
              <template #info
                > Java 17  Minecraft 1.18 </template
              >
            </SelectBlock>
          </el-col>
          <el-col :span="6" :offset="0">
            <SelectBlock style="min-height: 120px" @click="selectType(3)">
              <template #title> Ubuntu </template>
              <template #info> MC  Linux </template>
            </SelectBlock>
          </el-col>
        </el-row>
        <el-row :gutter="10" class="row-mt">
          <el-col :span="6" :offset="0">
            <SelectBlock style="min-height: 120px" @click="selectType(4)">
              <template #title> DockerFile </template>
              <template #info
                > DockerFile </template
              >
            </SelectBlock>
          </el-col>
        </el-row>
      </div>
    </template>
  </Panel>

  <Panel v-show="step == 2">
    <template #title>DockerFile</template>
    <template #default>
      <div class="row-mt">
        <div class="sub-title">
          <p class="sub-title-title"> DockerFile </p>
          <p class="sub-title-info">
            https://docs.docker.com/engine/reference/builder/
          </p>
        </div>
        <div class="sub-title">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"> /workspace </p>
        </div>
        <div class="row-mt">
          <el-input type="textarea" :rows="14" placeholder="" v-model="dockerFile">
          </el-input>
        </div>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
        </div>
        <div class="flex row-mt">
          <el-input
            v-model="name"
            size="small"
            style="width: 240px"
            placeholder="[]  openjdk"
          ></el-input>
          &nbsp;&nbsp;
          <el-input
            v-model="version"
            size="small"
            style="width: 240px"
            placeholder="[]  1.0 1.1 latest"
          ></el-input>
        </div>
      </div>
      <div class="row-mt">
        <el-button type="success" @click="createViaDockerFile" size="small"></el-button>
        <el-button type="" size="small" @click="forward(1)"></el-button>
      </div>
    </template>
  </Panel>

  <Panel v-show="step == 3">
    <template #title></template>
    <template #default>
      <div class="flex flex-space-between flex-align-items-center">
        <div>
          <ItemGroup>
            <el-button type="" size="small" @click="toProgress"></el-button>
            <el-button type="" size="small" @click="forward(1)"></el-button>
          </ItemGroup>
        </div>
        <span class="color-gray">&nbsp;&nbsp;</span>
      </div>
      <div class="row-mt">
        <el-row :gutter="10" v-if="progress">
          <el-col :span="6" :offset="0" v-for="(activity, index) in progress" :key="index">
            <LineOption class="row-mt" :custom="true">
              <template #default>
                <div style="font-size: 14px">
                  <span>{{ activity.name }}</span>
                  &nbsp;
                  <span>{{ activity.status }}</span>
                </div>
              </template>
            </LineOption>
          </el-col>
        </el-row>
        <div v-if="!progress" class="text-center">
          <p></p>
        </div>
      </div>
    </template>
  </Panel>
</template>

<script>
import Panel from "../../components/Panel";
import LineOption from "../../components/LineOption";
import SelectBlock from "../../components/SelectBlock";
import axios from "axios";
import { API_IMAGES, API_PROGRESS, API_SERVICE } from "../service/common";
import { request } from "../service/protocol";

export default {
  components: { Panel, SelectBlock, LineOption },
  data: function () {
    return {
      dockerFile: `FROM ubuntu:latest\nRUN mkdir -p /workspace\nWORKDIR /workspace\n`,
      step: 1,
      name: "",
      version: "",
      serviceUuid: this.$route.params.serviceUuid,
      progress: null
    };
  },
  methods: {
    forward(v) {
      this.step = v;
    },
    selectType(type) {
      if (type === 1) {
        this.dockerFile = `FROM openjdk:8
RUN mkdir -p /workspace
RUN apt update && apt install -y locales
RUN echo "zh_CN.UTF-8 UTF-8">/etc/locale.gen && locale-gen
ENV LANG=zh_CN.UTF-8
ENV LANGUAGE=zh_CN.UTF-8
ENV LC_ALL=zh_CN.UTF-8
ENV TZ=Asia/Shanghai
WORKDIR /workspace
`;
        this.name = "mopenjdk";
        this.version = "8";
      }
      if (type === 2) {
        this.dockerFile = `FROM openjdk:16.0.2
RUN mkdir -p /workspace
ENV TZ=Asia/Shanghai
WORKDIR /workspace
`;
        this.name = "mopenjdk";
        this.version = "16";
      }
      if (type === 3) {
        this.dockerFile = `FROM ubuntu:latest
RUN mkdir -p /workspace
RUN apt update
WORKDIR /workspace
`;
        this.name = "mubuntu";
        this.version = "latest";
      }
      if (type === 5) {
        this.dockerFile = `FROM openjdk:17
RUN mkdir -p /workspace
ENV LANG=zh_CN.UTF-8
ENV LANGUAGE=zh_CN.UTF-8
ENV LC_ALL=zh_CN.UTF-8
ENV TZ=Asia/Shanghai
WORKDIR /workspace
`;
        this.name = "mopenjdk";
        this.version = "17";
      }
      this.step = 2;
    },
    back() {
      this.$router.push({ path: `/image/${this.serviceUuid}` });
    },
    async toProgress() {
      this.step = 3;
      await this.getProgress();
      this.$message({ type: "info", message: "" });
    },
    async getProgress() {
      const progress = await request({
        methods: "POST",
        url: API_PROGRESS,
        params: {
          remote_uuid: this.serviceUuid
        }
      });
      const list = [];
      for (const k in progress) {
        if (progress[k] == 1) progress[k] = "...";
        if (progress[k] == -1) progress[k] = "...";
        if (progress[k] == 2) progress[k] = "";
        list.push({
          name: k,
          status: progress[k]
        });
      }
      this.progress = list;
    },
    async createViaDockerFile() {
      if (!this.name || !this.version || !this.dockerFile) {
        return this.$message({ type: "error", message: "" });
      }
      // eslint-disable-next-line no-unreachable
      await this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      });
      try {
        await request({
          method: "POST",
          url: API_IMAGES,
          params: {
            remote_uuid: this.serviceUuid
          },
          data: {
            dockerFile: this.dockerFile,
            name: this.name,
            tag: this.version
          }
        });
        this.$notify({
          title: "",
          message: ""
        });
      } catch (error) {
        this.$notify({
          title: "",
          message: error.toString(),
          type: "error"
        });
      } finally {
        this.step = 3;
        await this.getProgress();
      }
    }
  },
  async mounted() {
    const result = await axios.get(API_SERVICE);
    const responseObjects = result.data.data;
    responseObjects.forEach((v) => {
      this.services.push(v);
    });
  }
};
</script>

<style scoped>
.select-box-wrapper {
  display: flex;
  justify-content: center;
  align-content: center;
}
</style>
