import { io } from "socket.io-client";
import { ElNotification } from "element-plus";

// import { API_URL } from "./common";

export function connectRemoteService(
  addr,
  password,
  connectCallback = () => {},
  errorCallbackCallback = () => {}
) {
  console.log(":", addr);
  console.log(":", password);
  const socket = io(addr, {}).connect();

  socket.on("connect", () => {
    console.log("[WS->Daemon] Websocket ");
    socket.emit("stream/auth", {
      data: { password }
    });
  });

  socket.on("reconnect", () => {
    console.log("[WS->Daemon] Websocket ");
    socket.emit("stream/auth", {
      data: { password }
    });
  });

  socket.on("disconnect", () => {
    console.log("[WS->Daemon] Websocket ");
  });

  socket.on("connect_error", (error) => {
    console.log("[WS->Daemon] :", error);
    errorCallbackCallback(error);
  });

  socket.on("stream/auth", (packet) => {
    const data = packet.data;
    if (data === true) {
      console.log("[WS->Daemon/stream/auth] Websocket ");
      connectCallback();
    } else {
      console.log(":", data);
      ElNotification({
        title: "",
        message: `<span style="font-size: 12px;">(stream/auth)</span>`,
        dangerouslyUseHTMLString: true,
        type: "error",
        duration: 0
      });
    }
  });

  // eslint-disable-next-line no-unused-vars
  socket.on("instance/stdout", (packet) => {
    // console.log("Event: instance/stdout data:", packet);
  });

  socket.on("instance/stopped", (packet) => {
    console.log("Event: ", data);
    const data = packet.data;
    ElNotification({
      title: "",
      message: `<span style="font-size: 12px;">${data.instanceName}<br>${data.instanceUuid}</span>`,
      dangerouslyUseHTMLString: true,
      type: "success",
      duration: 3000
    });
  });

  socket.on("instance/opened", (packet) => {
    const data = packet.data;
    console.log("Event: :", data);
    ElNotification({
      title: "",
      message: `<span style="font-size: 12px;">${data.instanceName}<br>${data.instanceUuid}</span>`,
      dangerouslyUseHTMLString: true,
      type: "success",
      duration: 3000
    });
  });

  socket.on("instance/failure", (packet) => {
    const data = packet.data;
    console.log("Event: :", data);
    ElNotification({
      title: "",
      message: `<span style="font-size: 12px;">${data.instanceName}<br>${data.instanceUuid}<br></span>`,
      dangerouslyUseHTMLString: true,
      type: "error",
      duration: 5000
    });
  });

  socket.on("forward/instance", (packet) => {
    const data = packet.data;
    if (data === true) {
      console.log("Websocket IO");
    } else {
      console.log("Websocket IO", data);
    }
  });

  return socket;
}
