<template>
  <el-row :gutter="20">
    <el-col :md="6">
      <Panel>
        <template #title></template>
        <template #default>
          <div v-if="!available">
            <el-skeleton :rows="3" animated />
          </div>
          <div v-else>
            <LineInfo>
              <i class="el-icon-edit"></i> : {{ instanceInfo.config.nickname }}
            </LineInfo>
            <LineInfo>
              <i class="el-icon-tickets"></i> : {{ typeToText(instanceInfo.config.type) }}
            </LineInfo>
            <LineInfo
              ><i class="el-icon-finished"></i> :
              <!-- {{ codeToText(instanceInfo.status) }} -->
              <span v-if="instanceInfo.status === -1" class="color-red"></span>
              <span v-else-if="instanceInfo.status === 0" class="color-gray"></span>
              <span v-else-if="instanceInfo.status === 1" class="color-yellow"></span>
              <span v-else-if="instanceInfo.status === 2" class="color-yellow"></span>
              <span v-else-if="instanceInfo.status === 3" class="color-green"></span>
              <span v-else class="color-red"></span>
            </LineInfo>
            <LineInfo v-if="instanceInfo.info && instanceInfo.info.currentPlayers != -1">
              <i class="el-icon-user"></i> : {{ instanceInfo.info.currentPlayers }} /
              {{ instanceInfo.info.maxPlayers }}
            </LineInfo>
            <LineInfo v-if="instanceInfo.info && instanceInfo.info.version">
              <i class="el-icon-user"></i> : {{ instanceInfo.info.version }}
            </LineInfo>
          </div>
        </template>
      </Panel>
      <Panel>
        <template #title></template>
        <template #default>
          <div v-loading="busy">
            <el-row type="flex" justify="space-between" :gutter="10">
              <el-col :lg="24" v-show="instanceInfo.status === 0">
                <el-popconfirm title="" @confirm="openInstance">
                  <template #reference>
                    <el-button
                      icon="el-icon-video-play"
                      type="warning"
                      style="width: 100%"
                      size="small"
                      plain
                    >
                      
                    </el-button>
                  </template>
                </el-popconfirm>
              </el-col>
              <el-col :lg="24" v-show="instanceInfo.status === 3">
                <el-popconfirm title="" @confirm="stopInstance">
                  <template #reference>
                    <el-button
                      icon="el-icon-video-pause"
                      style="width: 100%"
                      size="small"
                      class="row-mt"
                      >
                    </el-button>
                  </template>
                </el-popconfirm>
              </el-col>
              <el-col :lg="24" v-show="instanceInfo.status === 3">
                <el-popconfirm title="" @confirm="restartInstance">
                  <template #reference>
                    <el-button
                      icon="el-icon-refresh-right"
                      style="width: 100%"
                      size="small"
                      class="row-mt"
                    >
                      
                    </el-button>
                  </template>
                </el-popconfirm>
              </el-col>
              <el-col :lg="24" v-show="instanceInfo.status > 0">
                <el-popconfirm title="" @confirm="killInstance">
                  <template #reference>
                    <el-button
                      icon="el-icon-switch-button"
                      type="danger"
                      plain
                      style="width: 100%"
                      size="small"
                      class="row-mt"
                      >
                    </el-button>
                  </template>
                </el-popconfirm>
              </el-col>
              <el-col :lg="24" v-show="instanceInfo.status === -1">
                <el-popconfirm title="" @confirm="stopAsynchronousTask">
                  <template #reference>
                    <el-button
                      icon="el-icon-switch-button"
                      type="danger"
                      plain
                      style="width: 100%"
                      size="small"
                      class="row-mt"
                      >
                    </el-button>
                  </template>
                </el-popconfirm>
              </el-col>
              <el-col
                :lg="24"
                v-show="instanceInfo.config.updateCommand && instanceInfo.status === 0"
              >
                <el-popconfirm title="" @confirm="updateInstace">
                  <template #reference>
                    <el-button
                      icon="el-icon-files"
                      plain
                      style="width: 100%"
                      size="small"
                      class="row-mt"
                      >/
                    </el-button>
                  </template>
                </el-popconfirm>
              </el-col>
            </el-row>
          </div>
        </template>
      </Panel>
      <Panel>
        <template #title></template>
        <template #default>
          <el-row :gutter="10">
            <!-- <el-col :span="12" :offset="0">
              <el-button style="width: 100%" size="small"> <span class="color-green"></span>
              </el-button>
            </el-col> -->
            <el-col :lg="12" :offset="0" class="row-mb">
              <el-button
                :disabled="!instanceInfo.config.type"
                icon="el-icon-s-operation"
                style="width: 100%"
                size="small"
                @click="toProcessConfig"
                >
              </el-button>
            </el-col>
            <el-col :lg="12" :offset="0" class="row-mb">
              <el-button
                :disabled="!available"
                icon="el-icon-monitor"
                style="width: 100%"
                size="small"
                @click="toTerminalSettingPanel"
                >
              </el-button>
            </el-col>

            <el-col :lg="12" :offset="0" class="row-mb">
              <el-button
                :disabled="!available"
                icon="el-icon-mobile"
                style="width: 100%"
                size="small"
                @click="toSchedule"
                >
              </el-button>
            </el-col>
            <el-col :lg="12" :offset="0" class="row-mb">
              <el-button
                :disabled="!available"
                icon="el-icon-data-line"
                style="width: 100%"
                size="small"
                @click="toPingPanel"
                >
              </el-button>
            </el-col>
            <el-col :lg="12" :offset="0" class="row-mb">
              <el-button
                :disabled="!available"
                icon="el-icon-notebook-2"
                style="width: 100%"
                size="small"
                @click="toEventPanel"
                >
              </el-button>
            </el-col>
            <el-col :lg="12" :offset="0" class="row-mb">
              <el-button
                :disabled="!available"
                icon="el-icon-folder-opened"
                style="width: 100%"
                size="small"
                @click="toFileManager"
                >
              </el-button>
            </el-col>
            <el-col :lg="24" :offset="0" v-if="isTopPermission">
              <el-button
                :disabled="!available"
                icon="el-icon-setting"
                style="width: 100%"
                size="small"
                @click="toInstanceDetail"
                >
              </el-button>
            </el-col>
          </el-row>
        </template>
      </Panel>
      <Panel>
        <template #title></template>
        <template #default>
          <div v-if="!available">
            <el-skeleton :rows="5" animated />
          </div>
          <div v-else>
            <LineInfo>
              <div class="text-overflow-ellipsis">
                <i class="el-icon-document"></i>
                <span style="font-size: 12px"> GID {{ serviceUuid }}</span>
              </div>
            </LineInfo>
            <LineInfo>
              <div class="text-overflow-ellipsis">
                <i class="el-icon-document"></i>
                <span class="text-overflow-ellipsis" style="font-size: 12px">
                  UID {{ instanceInfo.instanceUuid }}</span
                >
              </div>
            </LineInfo>
            <LineInfo>
              <i class="el-icon-date"></i> :
              {{
                instanceInfo.config.endTime
                  ? new Date(instanceInfo.config.endTime).toLocaleDateString()
                  : ""
              }}
            </LineInfo>
            <LineInfo>
              <i class="el-icon-date"></i> :
              {{ instanceInfo.config.createDatetime }}
            </LineInfo>
            <LineInfo>
              <i class="el-icon-date"></i> :
              {{ instanceInfo.config.lastDatetime }}
            </LineInfo>
            <!-- <LineInfo><i class="el-icon-document"></i> : {{ instanceInfo.tag }}</LineInfo> -->
            <LineInfo
              ><i class="el-icon-document"></i> : {{ instanceInfo.config.ie }} :
              {{ instanceInfo.config.oe }}
            </LineInfo>
          </div>
        </template>
      </Panel>
    </el-col>
    <el-col :md="18">
      <Panel v-loading="!available" element-loading-text="">
        <template #title></template>
        <template #default>
          <div class="terminal-wrapper">
            <div id="terminal-container" style="height: 560px; width: 100%"></div>
            <div id="terminal-input-wrapper">
              <el-input
                placeholder=""
                prefix-icon="el-icon-arrow-right"
                size="mini"
                v-model="command"
                ref="terminalCommandInput"
                @keyup.enter="sendCommand(command)"
              >
              </el-input>
            </div>
          </div>
        </template>
      </Panel>
      <Panel>
        <template #title></template>
        <template #default>
          <div v-if="commandhistory.length > 0">
            <ItemGroup>
              <el-tag
                v-for="(item, index) in commandhistory"
                :key="index"
                @click="selectHistoryCommand(item)"
                size="small"
                type="info"
                class="text-overflow-ellipsis"
                style="max-width: 23%; cursor: pointer; font-size: 13px"
              >
                {{ item }}
              </el-tag>
            </ItemGroup>
          </div>
          <div v-else>
            <p class="color-gray"></p>
          </div>
        </template>
      </Panel>
      <Panel v-if="isShowPlayersChart">
        <template #title></template>
        <template #default>
          <p>1010</p>
          <div class="echart-wrapper">
            <div id="echart-wrapper-players" style="width: 100%; height: 200px"></div>
          </div>
        </template>
      </Panel>
    </el-col>
  </el-row>

  <Dialog v-model="pingConfigForm.is">
    <template #title></template>
    <template #default>
      <div class="sub-title">
        <p class="sub-title-title"></p>
        <p class="sub-title-info">
          
        </p>
      </div>
      <div class="sub-title">
        <p class="sub-title-title"></p>
        <p class="sub-title-info">
          IP
        </p>
      </div>
      <el-input v-model="pingConfigForm.ip" placeholder="localhost" size="small"></el-input>
      <div class="sub-title row-mt">
        <p class="sub-title-title"></p>
        <p class="sub-title-info"></p>
      </div>
      <el-input v-model="pingConfigForm.port" placeholder=" 25565" size="small"></el-input>
      <div class="row-mt">
        <ItemGroup>
          <el-button type="success" size="small" @click="instanceConfigUpdate"></el-button>
          <el-button @click="pingConfigForm.is = !pingConfigForm.is" size="small"></el-button>
        </ItemGroup>
      </div>
    </template>
  </Dialog>

  <Dialog v-model="eventConfigPanel.visible">
    <template #title></template>
    <template #default>
      <div class="sub-title">
        <p class="sub-title-title"></p>
        <p class="sub-title-info">
          <br />
        </p>
        <div class="row-mt">
          <el-switch v-model="eventConfigPanel.autoRestart"></el-switch>
        </div>
      </div>

      <div class="sub-title">
        <p class="sub-title-title"></p>
        <p class="sub-title-info">
          <br />
        </p>
        <div class="row-mt">
          <el-switch v-model="eventConfigPanel.autoStart"></el-switch>
        </div>
      </div>

      <div class="row-mt">
        <ItemGroup>
          <el-button type="success" size="small" @click="instanceConfigUpdate"></el-button>
          <el-button size="small" @click="eventConfigPanel.visible = false"></el-button>
        </ItemGroup>
      </div>
    </template>
  </Dialog>

  <Dialog v-model="terminalSettingPanel.visible">
    <template #title></template>
    <template #default>
      <div class="sub-title">
        <p class="sub-title-title"></p>
        <p class="sub-title-info">
          <br />
        </p>
        <div class="row-mt">
          <el-switch v-model="terminalSettingPanel.haveColor"></el-switch>
        </div>
      </div>
      <div class="row-mt">
        <ItemGroup>
          <el-button type="success" size="small" @click="instanceConfigUpdate"></el-button>
          <el-button size="small" @click="terminalSettingPanel.visible = false"></el-button>
        </ItemGroup>
      </div>
    </template>
  </Dialog>

  <Dialog v-model="unavailableTerminal" style="z-index: 9999">
    <template #title></template>
    <template #default>
      <div class="sub-title">
        <p class="sub-title-title">
          {{ unavailableIp ? ` ${unavailableIp}` : "" }}
        </p>
        <p class="sub-title-info">
          <span></span>
        </p>
        <div>
          <img
            :src="require('../../assets/daemon_connection_error.png')"
            alt=""
            srcset=""
            style="width: 460px"
          />
        </div>
        <div class="sub-title"></div>
        <ol style="padding-left: 20px">
          <li></li>
          <li>
            FRPHTTPS  wss:// 
            <br />
             HTTPSWSS 
          </li>
          <li>
            
            <a href="https://docs.mcsmanager.com" target="_blank" rel="noopener noreferrer"
              >https://docs.mcsmanager.com</a
            >
            
          </li>
        </ol>
      </div>
    </template>
  </Dialog>
</template>

<script>
import * as echarts from "echarts";
import Dialog from "../../components/Dialog";
import Panel from "../../components/Panel";
import "../../assets/xterm/xterm.css";
import LineInfo from "../../components/LineInfo";
import LineButton from "../../components/LineButton";
import { connectRemoteService } from "../service/socket";
import {
  API_INSTANCE,
  API_INSTANCE_KILL,
  API_INSTANCE_OPEN,
  API_INSTANCE_REMOTE_SERVICE_STREAM,
  API_INSTANCE_RESTART,
  API_INSTANCE_UPDATE,
  API_INSTANCE_STOP,
  API_INSTANCE_OUTPUT,
  API_INSTANCE_ASYNC_TASK,
  API_INSTANCE_ASYNC_STOP
} from "../service/common";
import router from "../router";
import { parseforwardAddress, request } from "../service/protocol";
import { encodeConsoleColor } from "../service/terminal_color";
import { ElNotification } from "element-plus";
import { statusCodeToText, typeTextToReadableText } from "../service/instance_tools";
import { initTerminalWindow, textToTermText } from "../service/term";
import { getPlayersOption } from "../service/chart_option";

export default {
  data: function () {
    return {
      serviceUuid: this.$route.params.serviceUuid,
      instanceUuid: this.$route.params.instanceUuid,
      term: null,
      terminalWidth: 0,
      terminalHeight: 0,
      command: "",
      available: false,
      socket: null,
      instanceInfo: {
        config: {}
      },
      renderTask: null,
      commandhistory: [],
      busy: false,

      bool: false,

      pingConfigForm: {
        is: false,
        ip: "",
        port: "",
        type: 1
      },

      eventConfigPanel: {
        visible: false,
        autoRestart: false,
        autoStart: false
      },

      terminalSettingPanel: {
        visible: false,
        haveColor: true
      },

      unavailableTerminal: false,
      unavailableIp: null,

      playersChart: null,
      isShowPlayersChart: false
    };
  },
  computed: {
    isStarted() {
      return this.instanceInfo.status !== 0;
    },
    isTopPermission() {
      return this.$store.state.userInfo.permission >= 10;
    }
  },
  // eslint-disable-next-line vue/no-unused-components
  components: { Panel, LineInfo, LineButton, Dialog },
  methods: {
    // (Ajax)
    async renderFromAjax() {
      try {
        const result = await request({
          method: "GET",
          url: API_INSTANCE,
          params: { uuid: this.instanceUuid, remote_uuid: this.serviceUuid }
        });
        this.instanceInfo = result;
        // console.log(":", this.instanceInfo);
      } catch (err) {
        console.log("", err);
      }
    },
    // Websocket
    async renderFromSocket() {
      this.sendResize(this.terminalWidth, this.terminalHeight);
      this.socket.emit("stream/detail", {});
    },
    // 
    async setUpWebsocket() {
      // 
      let res = null;
      try {
        res = await request({
          method: "POST",
          url: API_INSTANCE_REMOTE_SERVICE_STREAM,
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid }
        });
      } catch (error) {
        ElNotification({
          title: "",
          message: error,
          dangerouslyUseHTMLString: true,
          type: "error",
          duration: 0
        });
        this.unavailableTerminal = true;
        return;
      }

      // 
      const password = res.password;
      const addr = parseforwardAddress(res.addr, "ws");
      this.socket = connectRemoteService(
        addr,
        password,
        () => {
          this.unavailableIp = null;
          this.unavailableTerminal = false;
          // 
          this.syncLog();
        },
        () => {
          this.unavailableIp = addr;
          this.unavailableTerminal = true;
        }
      );

      // 
      this.socket.on("instance/stdout", (packet) => {
        if (this.instanceInfo?.config?.terminalOption?.haveColor) {
          this.term.write(encodeConsoleColor(packet.data.text));
        } else {
          this.term.write(textToTermText(packet.data.text));
        }
      });
      // 
      this.socket.on("stream/detail", (packet) => {
        this.instanceInfo = packet.data;
        this.initChart();
      });
      // 
      this.socket.on("disconnect", () => {
        this.available = false;
      });
      // 
      return new Promise((r) => {
        // 
        this.socket.on("connect", () => {
          this.available = true;
          r();
        });
      });
    },
    typeToText(v) {
      return typeTextToReadableText(v);
    },
    selectHistoryCommand(item) {
      this.command = item;
      console.log(this.$refs.terminalCommandInput.focus());
    },
    pushHistoryCommand(cmd) {
      if (cmd.trim().length <= 0) return;
      this.commandhistory.unshift(cmd);
      if (this.commandhistory.length > 40) {
        this.commandhistory.pop();
      }
      localStorage.setItem("CommandHistory", JSON.stringify(this.commandhistory));
    },
    startInterval() {
      if (!this.renderTask) this.renderTask = setInterval(this.renderFromSocket, 1000);
    },
    stopInterval() {
      clearInterval(this.renderTask);
    },
    //  Terminal 
    initTerm() {
      // 
      this.term = initTerminalWindow(document.getElementById("terminal-container"));
      this.term.onData(this.sendInput);
    },
    // Ajax
    async openInstance() {
      // this.busy = true;
      try {
        await request({
          method: "GET",
          url: API_INSTANCE_OPEN,
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid }
        });
      } catch (error) {
        this.$message({ message: error.toString(), type: "error" });
      } finally {
        setTimeout(() => (this.busy = false), 200);
      }
    },
    // Ajax
    async stopInstance() {
      // this.busy = true;
      try {
        await request({
          method: "GET",
          url: API_INSTANCE_STOP,
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid }
        });
      } catch (error) {
        this.$message({ message: error.toString(), type: "error" });
      } finally {
        setTimeout(() => (this.busy = false), 200);
      }
    },
    // 
    async stopAsynchronousTask() {
      try {
        await request({
          method: "GET",
          url: API_INSTANCE_ASYNC_STOP,
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid }
        });
      } catch (error) {
        this.$message({ message: error.toString(), type: "error" });
      } finally {
        setTimeout(() => (this.busy = false), 200);
      }
    },
    // 
    async updateInstace() {
      try {
        await request({
          method: "POST",
          url: API_INSTANCE_ASYNC_TASK,
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid, task_name: "update" },
          data: {
            time: new Date().getTime()
          }
        });
      } catch (error) {
        this.$message({ message: error.toString(), type: "error" });
      } finally {
        setTimeout(() => (this.busy = false), 200);
      }
    },
    // Ajax
    async killInstance() {
      // this.busy = true;
      try {
        await request({
          method: "GET",
          url: API_INSTANCE_KILL,
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid }
        });
      } catch (error) {
        this.$message({ message: error.toString(), type: "error" });
      } finally {
        setTimeout(() => (this.busy = false), 200);
      }
    },
    // Ajax
    async restartInstance() {
      // this.busy = true;
      try {
        await request({
          method: "GET",
          url: API_INSTANCE_RESTART,
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid }
        });
      } catch (error) {
        this.$message({ message: error.toString(), type: "error" });
      } finally {
        setTimeout(() => (this.busy = false), 2000);
      }
    },
    sendResize(w, h) {
      if (this.instanceInfo.config.processType !== "docker") return;
      if (!this.socket || !this.available) return;
      if (!this.isStarted) return;
      this.socket.emit("stream/resize", {
        data: { w, h }
      });
    },
    // Websocket
    sendInput(input) {
      //  Docker 
      if (this.instanceInfo.config.processType !== "docker") return;
      if (!this.socket || !this.available)
        return this.$message({ message: "", type: "error" });
      if (!this.isStarted)
        return this.$message({ message: "", type: "error" });
      this.socket.emit("stream/write", {
        data: { input }
      });
    },
    // Websocket
    sendCommand(command, method) {
      if (!this.socket || !this.available)
        return this.$message({ message: "", type: "error" });
      if (!this.isStarted)
        return this.$message({ message: "", type: "error" });
      if (method !== 1) this.pushHistoryCommand(command);
      this.socket.emit("stream/input", {
        data: { command }
      });
      this.command = "";
    },
    // 
    toFileManager() {
      router.push({ path: `/file/${this.serviceUuid}/${this.instanceUuid}/` });
    },
    toProcessConfig() {
      router.push({
        path: `/process_config/${this.serviceUuid}/${this.instanceUuid}/`,
        query: {
          type: this.instanceInfo.config.type
        }
      });
    },
    toSchedule() {
      router.push({ path: `/schedule/${this.serviceUuid}/${this.instanceUuid}/` });
    },
    toPingPanel() {
      if (this.instanceInfo.config && this.instanceInfo.config.pingConfig) {
        this.pingConfigForm.ip = this.instanceInfo.config.pingConfig.ip;
        this.pingConfigForm.port = this.instanceInfo.config.pingConfig.port;
      }
      this.pingConfigForm.is = true;
    },
    toEventPanel() {
      if (this.instanceInfo.config && this.instanceInfo.config.eventTask) {
        this.eventConfigPanel.autoRestart = this.instanceInfo.config.eventTask.autoRestart;
        this.eventConfigPanel.autoStart = this.instanceInfo.config.eventTask.autoStart;
      }
      this.eventConfigPanel.visible = true;
    },
    toTerminalSettingPanel() {
      this.terminalSettingPanel.visible = true;
      this.terminalSettingPanel.haveColor = this.instanceInfo.config.terminalOption.haveColor;
    },
    async syncLog() {
      try {
        const text = await request({
          url: API_INSTANCE_OUTPUT,
          method: "GET",
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid }
        });
        this.term.clear();
        if (this.instanceInfo?.config?.terminalOption?.haveColor) {
          this.term.write(encodeConsoleColor(text));
        } else {
          this.term.write(text);
        }
        this.term.scrollToBottom();
      } catch (error) {
        this.term.write(error);
      }
    },
    // 
    async instanceConfigUpdate() {
      try {
        await request({
          method: "PUT",
          url: API_INSTANCE_UPDATE,
          params: { remote_uuid: this.serviceUuid, uuid: this.instanceUuid },
          data: {
            pingConfig: this.pingConfigForm,
            eventTask: this.eventConfigPanel,
            terminalOption: this.terminalSettingPanel
          }
        });
        this.$message({
          type: "success",
          message: ""
        });
        this.pingConfigForm.is = false;
        this.eventConfigPanel.visible = false;
        this.terminalSettingPanel.visible = false;
      } catch (error) {
        this.$message({
          type: "error",
          message: error.message
        });
      }
    },
    codeToText(p) {
      return statusCodeToText(p);
    },
    initStorage() {
      const ch = localStorage.getItem("CommandHistory");
      // 
      if (ch) {
        this.commandhistory = JSON.parse(ch);
      } else {
        localStorage.setItem("CommandHistory", JSON.stringify([]));
      }
    },
    toInstanceDetail() {
      this.$router.push({ path: `/instance_detail/${this.serviceUuid}/${this.instanceUuid}/` });
    },
    initChart() {
      if (!this.instanceInfo.info.playersChart || !this.instanceInfo.info.playersChart.length) {
        this.isShowPlayersChart = false;
        return;
      }
      if (!this.isShowPlayersChart) {
        this.isShowPlayersChart = true;
        setTimeout(() => {
          // domecharts
          this.playersChart = echarts.init(document.getElementById("echart-wrapper-players"));
          this.playersChart.setOption(getPlayersOption());
          this.setPlayersChart();
        }, 200);
      } else {
        this.setPlayersChart();
      }
    },
    setPlayersChart() {
      if (!this.playersChart) return;
      const MAX_TIME = this.instanceInfo.info.playersChart.length - 1;
      const source = this.instanceInfo.info.playersChart;
      for (const key in source) {
        source[key]["time"] = `${(MAX_TIME - key) * 10} `;
      }
      this.playersChart.setOption({
        dataset: {
          dimensions: ["time", "value"],
          source
        }
      });
    }
  },
  // 
  async mounted() {
    try {
      // Web
      this.initStorage();

      // 
      this.initTerm();
      this.term.onResize((size) => {
        this.terminalHeight = size.rows;
        this.terminalWidth = size.cols;
        this.sendResize(size.cols, size.rows);
      });
      this.term.fitAddon.fit();
      window.onresize = () => {
        this.term.fitAddon.fit();
      };

      //  Websocket 
      await this.setUpWebsocket();
      //  & 
      await this.renderFromSocket();
      this.startInterval();
    } catch (error) {
      console.error(error);
      // 
    }
  },
  // 
  beforeUnmount() {
    try {
      // 
      this.stopInterval();
      // 
      this.socket.disconnect();
      // 
      this.term.dispose();
    } catch (error) {
      // 
      console.error(error);
    }
  }
};
</script>

<style scoped>
.terminal-wrapper {
  background-color: rgb(30, 30, 30);
  padding: 4px;
  border-radius: 4px;
  /* overflow: hidden; */
}

#terminal-input-wrapper input {
  width: 100%;
  font-size: 12px;
}
</style>
