<template>
  <el-row :gutter="20">
    <el-col :span="24">
      <Panel>
        <template #title></template>
        <template #default>
          <el-row :gutter="20" class="row-mb">
            <el-col :md="12" :offset="0" class="col-md-responsive">
              <el-input
                v-model="query.userName"
                type="text"
                placeholder=""
                size="small"
                style="width: 180px; margin-right: 10px"
                autocomplete="off"
                :readonly="readonly"
                @focus="() => (readonly = false)"
              ></el-input>
              <el-button size="small" type="primary" @click="refresh">
                <i class="el-icon-refresh"></i> /
              </el-button>
            </el-col>
            <el-col :md="12" :offset="0" class="text-align-right col-md-responsive">
              <el-button size="small" type="success" @click="toNewUserPanel">
                <i class="el-icon-plus"></i> 
              </el-button>
              <el-button size="small" type="danger" @click="deleteUser">
                <i class="el-icon-delete"></i> 
              </el-button>
            </el-col>
          </el-row>

          <div class="instance-table-warpper">
            <div></div>
            <div>
              <el-pagination
                background
                layout="prev, pager, next"
                :total="maxPage"
                v-model:currentPage="page"
                :page-size="1"
                @current-change="handleCurrentChange"
                small
              ></el-pagination>
            </div>
          </div>

          <el-table
            :data="objects"
            stripe
            style="width: 100%"
            size="small"
            ref="multipleTable"
            @selection-change="selectionChange"
          >
            <el-table-column type="selection" width="55"> </el-table-column>
            <el-table-column prop="uuid" label="UUID" width="240"></el-table-column>
            <el-table-column prop="userName" label=""></el-table-column>
            <el-table-column prop="permission" label=""></el-table-column>
            <el-table-column prop="registerTime" label=""></el-table-column>
            <el-table-column prop="loginTime" label=""></el-table-column>
            <el-table-column label="" style="text-align: center" width="180">
              <template #default="scope">
                <el-button size="mini" @click="toEditUserPanel(scope.row)"></el-button>
                <el-button size="mini" @click="toAssignPanel(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </Panel>
    </el-col>
  </el-row>

  <!--  -->
  <Dialog v-model="isNewUser">
    <template #title></template>
    <template #default>
      <div>
        <div class="sub-title">
          <p class="sub-title-title"></p>
          <p class="sub-title-info">612</p>
        </div>
        <el-input
          v-model="newUserInfo.userName"
          placeholder="..."
          size="small"
        ></el-input>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info">618</p>
        </div>
        <el-input
          v-model="newUserInfo.password"
          placeholder="618"
          size="small"
          type="text"
        ></el-input>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"></p>
        </div>
        <el-select v-model="newUserInfo.permission" placeholder="" size="small">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="10"></el-option>
          <el-option label="" :value="-1"></el-option>
        </el-select>

        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info">
             Linux  Docker
            
            <br />
            <a
              class="color-blue"
              href="https://docs.mcsmanager.com/"
              target="_blank"
              rel="noopener noreferrer"
              ></a
            >
          </p>
        </div>
        <div class="row-mt">
          <el-button type="success" size="small" @click="createUser"></el-button>
          <el-button @click="cancelNewPanel" size="small"></el-button>
        </div>
      </div>
    </template>
  </Dialog>

  <!--  -->
  <Dialog v-model="isEditUser">
    <template #title></template>
    <template #default>
      <div>
        <div class="sub-title">
          <p class="sub-title-title"></p>
          <p class="sub-title-info">612</p>
        </div>
        <el-input
          v-model="editUserInfo.userName"
          placeholder="..."
          size="small"
        ></el-input>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"></p>
        </div>
        <el-input v-model="editUserInfo.passWord" placeholder="" size="small"></el-input>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"></p>
        </div>
        <el-select v-model="editUserInfo.permission" placeholder="" size="small">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="10"></el-option>
          <el-option label="" :value="-1"></el-option>
        </el-select>
        <div class="row-mt">
          <el-button type="success" size="small" @click="updateUser"></el-button>
          <el-button @click="cancelEditPanel" size="small"></el-button>
        </div>
      </div>
    </template>
  </Dialog>
</template>

<script>
import Panel from "../../components/Panel";
import Dialog from "../../components/Dialog";
import { request } from "../service/protocol";
import { API_SERVICE, API_USER, API_SEARCH } from "../service/common";

export default {
  components: { Panel, Dialog },
  data() {
    return {
      page: 1,
      maxPage: 1,
      query: {
        userName: ""
      },
      newUserInfo: {
        userName: "",
        password: "",
        permission: 1
      },
      isEditUser: false,
      isNewUser: false,
      // isAssign: false,
      // isAssignLoading: true,
      editUserInfo: {},
      objects: [],
      remoteObjects: [], // 
      instances: [], // 
      multipleSelection: [], // 

      readonly: true
    };
  },
  async mounted() {
    // 
    await this.render();
    // 
    this.renderServices();
  },
  methods: {
    // 
    async render() {
      const result = await request({
        method: "GET",
        url: API_SEARCH,
        params: {
          userName: this.query.userName,
          page: this.page,
          page_size: 50
        }
      });
      this.maxPage = result.maxPage;
      this.page = result.page;
      this.objects = result.data;
      this.query.userName = "";
    },
    async renderServices() {
      this.remoteObjects = await request({
        method: "GET",
        url: API_SERVICE
      });
      this.remoteObjects.forEach((v) => {
        v.instances.forEach((instance) => {
          this.instances.push({
            instanceUuid: instance.instanceUuid,
            serviceUuid: v.uuid,
            nickname: instance.config.nickname
          });
        });
      });
    },
    async requestUserInfo(uuid) {
      return await request({
        method: "GET",
        url: API_USER,
        params: {
          uuid: uuid,
          advanced: true
        }
      });
    },
    // 
    handleCurrentChange() {
      this.refresh();
    },
    // 
    selectionChange(v) {
      this.multipleSelection = v;
    },
    toEditUserPanel(row) {
      this.editUserInfo = row;
      this.isEditUser = true;
    },
    toNewUserPanel() {
      this.isNewUser = true;
    },
    async cancelEditPanel() {
      this.isEditUser = false;
      await this.render();
    },
    async cancelNewPanel() {
      this.isNewUser = false;
      await this.render();
    },
    async toAssignPanel(row) {
      const uuid = row.uuid;
      this.$router.push({ path: `/user_resources/${uuid}` });
      // this.isAssign = true;
      // this.isAssignLoading = true;
      // this.editUserInfo = {};
      // // 
      // this.editUserInfo = await this.requestUserInfo(row.uuid);
      // this.isAssignLoading = false;
    },
    assignInstance(row) {
      this.editUserInfo.instances.push({
        uuid: row.instanceUuid,
        region: row.serviceUuid,
        instanceInfo: {
          config: {
            nickname: row.nickname
          }
        }
      });
    },
    cancelInstace(uuid) {
      const targetUuid = uuid;
      this.editUserInfo.instances.forEach((v, index) => {
        if (v.uuid == targetUuid) this.editUserInfo.instances.splice(index, 1);
      });
    },
    async updateUser() {
      try {
        await request({
          method: "PUT",
          url: API_USER,
          data: {
            uuid: this.editUserInfo.uuid,
            config: this.editUserInfo
          }
        });
        this.$message({ type: "success", message: "" });
        this.isEditUser = false;
        this.isAssign = false;
      } catch (error) {
        this.$message({
          type: "error",
          message: `: ${error.message}`
        });
      }
      this.refresh();
    },
    async createUser() {
      try {
        await request({
          method: "POST",
          url: API_USER,
          data: {
            username: this.newUserInfo.userName,
            password: this.newUserInfo.password,
            permission: this.newUserInfo.permission
          }
        });
        this.$message({ type: "success", message: "" });
        this.isNewUser = !this.isNewUser;
      } catch (error) {
        this.$message({
          type: "error",
          message: `: ${error.message}`
        });
      }
      this.refresh();
    },
    async deleteUser() {
      try {
        const uuids = [];
        for (const v of this.multipleSelection) {
          uuids.push(v.uuid);
        }
        if(!uuids.length){
          throw new Error("");
        }
        await request({
          method: "DELETE",
          url: API_USER,
          data: uuids
        });
        this.$message({ type: "success", message: "" });
      } catch (error) {
        this.$message({
          type: "error",
          message: `: ${error.message}`
        });
      }
      this.refresh();
    },
    refresh() {
      this.render();
    }
  }
};
</script>
